@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");

$screen-sm-min: 576px;
$screen-md-min: 768px;
$screen-xl-min: 1248px;
$black: #000000;
$white: #ffffff;
$yellow: #ebff00;
$transparentYellow: #ebff0099;
$pink: #ff70d9;

@mixin card__title-md {
  max-width: 230px;
  font-size: 40px;
  font-weight: 800;
  line-height: 40px;
  text-align: left;
}

@mixin card__subtitle-md {
  font-size: 16px;
  font-weight: 800;
  line-height: 19px;
  text-align: left;
}

@mixin card__title-xl {
  max-width: 634px;
  font-size: 74px;
  font-weight: 800;
  line-height: 74px;
  text-align: left;
  color: $white;
}

@mixin card__subtitle-xl {
  font-size: 26px;
  font-weight: 800;
  line-height: 31px;
  text-align: left;
  margin-top: 22px;
  color: $white;
}

@mixin card__title {
  max-width: 260px;
  font-size: 46px;
  font-weight: 800;
  line-height: 46px;
  text-align: left;
  color: $white;
}

@mixin card__subtitle {
  font-size: 24px;
  font-weight: 800;
  line-height: 29px;
  text-align: left;
  color: $white;
}

p,
h1,
h2,
button,
label {
  margin: 0;
  font-family: Lato;
  letter-spacing: 0em;
}

.header {
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  height: 80px;
  width: 100vw;
  background-color: $black;

  @media (max-width: $screen-md-min) {
    height: 48px;
  }
}

.header__cart {
  width: 25px;
  height: 20px;
  cursor: pointer;
  background-image: url(./cart.svg);

  &:hover {
    background-image: url(./cartHovered.svg);
  }
}

.header__logo {
  width: 104px;
  height: 35px;
  cursor: pointer;
  padding-left: 81px;

  @media (max-width: $screen-md-min) {
    width: 71px;
    height: 24px;
    padding-left: 20px;
  }

  @media (max-width: $screen-xl-min) {
    width: 71px;
    height: 24px;
    padding-left: 30px;
  }
}

.banner {
  position: relative;
  background: url("./earth.webp"),
    linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) -0.52%,
      rgba(15, 20, 32, 0.79) 99.48%
    ),
    url(./banner.webp);
  background-size: 327px 367px, cover, cover;
  background-repeat: no-repeat;
  background-position: calc(100% - 84px) 139px, center, center;
  min-height: 570px;

  @media (max-width: $screen-xl-min) {
    background-size: 300px 336px, cover, cover;
    background-position: calc(100% + 74px) 106px, center, center;
    min-height: 500px;
  }

  @media (max-width: $screen-md-min) {
    background-position: 50% -57px, center, center;
    min-height: 499px;
  }
}

.banner__container {
  padding: 156px 0 0 91px;
  max-width: 693px;

  @media (max-width: $screen-xl-min) {
    padding: 180px 0 100px 30px;
    max-width: 441px;
  }

  @media (max-width: $screen-md-min) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 298px 34px 44px 33px;
    max-width: none;
  }
}

.banner__title {
  font-size: 72px;
  font-weight: 800;
  line-height: 72px;
  text-align: left;
  color: $white;
  span {
    color: $pink;
  }

  @media (max-width: $screen-xl-min) {
    font-size: 48px;
    line-height: 48px;
  }

  @media (max-width: $screen-md-min) {
    font-size: 36px;
    line-height: 36px;
    text-align: center;
  }
}

.banner__subtitle {
  margin: 22px 0;
  font-size: 32px;
  font-weight: 800;
  line-height: 38px;
  text-align: left;
  color: $white;

  span {
    color: $yellow;
  }

  @media (max-width: $screen-xl-min) {
    font-size: 22px;
    line-height: 26px;
  }

  @media (max-width: $screen-md-min) {
    margin: 17px 0;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }
}

.button_outlined {
  font-size: 22px;
  font-weight: 700;
  line-height: 26px;
  text-align: left;
  padding: 14px 40px;
  border-radius: 100px;
  border: 2px solid #ebff00;
  color: $yellow;
  background: none;
  margin-top: 22px;
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  @media (max-width: $screen-xl-min) {
    font-size: 16px;
    line-height: 19px;
    padding: 7px 40px;
  }
}

.button_filled {
  cursor: pointer;
  font-size: 22px;
  font-weight: 700;
  line-height: 26px;
  text-align: left;
  color: $black;
  background-color: $yellow;
  padding: 14px 40px;
  border-radius: 100px;
  border: none;

  &:hover {
    background-color: $transparentYellow;
  }

  @media (max-width: $screen-xl-min) {
  }

  @media (max-width: $screen-md-min) {
    padding: 7px 40px;
    font-size: 16px;
    line-height: 19px;
  }
}

.offers {
  margin: 58px 80px 0;

  @media (max-width: $screen-xl-min) {
    margin: 55px 30px 0;
  }

  @media (max-width: $screen-md-min) {
    margin: 50px 20px 0;
  }
}

.offers__title {
  font-size: 32px;
  font-weight: 800;
  line-height: 38px;
  text-align: left;
  color: $white;

  @media (max-width: $screen-xl-min) {
    font-size: 24px;
    line-height: 29px;
  }

  @media (max-width: $screen-md-min) {
    font-size: 18px;
    line-height: 22px;
  }
}

.offers__list {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @media (max-width: $screen-xl-min) {
    margin-top: 30px;
    gap: 30px;
  }

  @media (max-width: $screen-md-min) {
    grid-template-columns: 1fr;
  }
}

.offers__card {
  border-radius: 10px;
  container-type: inline-size;
  background-size: 100% 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  padding: 35px;

  h1 {
    @include card__title-xl;
  }

  h2 {
    @include card__subtitle-xl;
  }

  &:nth-child(1) {
    grid-column-start: 1;
    grid-column-end: 3;
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      ),
      url(./cardMove.webp);
  }

  &:nth-child(2) {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      ),
      url(./cardSpace.webp);
  }

  &:nth-child(3) {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      ),
      url(./cardDream.webp);
  }

  &:nth-child(4) {
    grid-column-start: -1;
    grid-column-end: -3;
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      ),
      url(./cardFantastic.webp);
  }

  @container (max-width: 608px) {
    h1 {
      @include card__title;
    }

    h2 {
      @include card__subtitle;
    }
  }

  @media (max-width: $screen-xl-min) {
    padding: 107px 0 27px 27px;

    h1 {
      @include card__title-md();
    }

    h2 {
      @include card__subtitle-md();

      span {
        display: none;
      }
    }

    &:nth-child(1) {
      grid-column-start: auto;
      grid-column-end: auto;
    }

    &:nth-child(4) {
      grid-column-start: auto;
      grid-column-end: auto;
    }
  }
}

.footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: $black;
  height: 127px;
  width: 100vw;
}

.footer__description {
  font-size: 18px;
  font-weight: 800;
  line-height: 28px;
  text-align: center;
  color: $yellow;
}

.menu {
  display: flex;
  align-items: center;
  color: $white;
  gap: 0 30px;
  padding-right: 81px;

  @media (max-width: $screen-xl-min) {
    padding-right: 50px;
  }

  @media (max-width: $screen-md-min) {
    display: none;
  }
}

.menu div p {
  cursor: pointer;
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
  text-align: center;

  &:hover {
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(98.25deg, #ebff00 18.56%, #ff70d9 76.02%);
  }
}

.hamburger__menu {
  display: none;
  flex-direction: row;
  list-style-type: none;
  margin: 0;
  padding: 0;

  @media (max-width: $screen-md-min) {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 48px;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
}

.hamburger__menu > li {
  margin: 0 1rem;
  overflow: hidden;
  align-items: center;

  @media (max-width: $screen-md-min) {
    display: flex;
    justify-content: center;
    cursor: pointer;
    margin: 0;
    padding: 0.5em 0;
    width: 100%;
    color: $white;
    background-color: $black;

    &:hover {
      p {
        background-clip: text;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(
          98.25deg,
          #ebff00 18.56%,
          #ff70d9 76.02%
        );
      }

      div {
        background-image: url(./cartHovered.svg);
      }
    }

    p {
      font-size: 14px;
      font-weight: 700;
      line-height: 22px;
      text-align: center;
    }
  }
}

.hamburger__menu-button-container {
  display: none;
  padding-right: 20px;
  height: 100%;
  width: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: $screen-md-min) {
    display: flex;
  }
}

#hamburger__menu-toggle {
  display: none;
}

.hamburger__menu-button,
.hamburger__menu-button::before,
.hamburger__menu-button::after {
  display: block;
  background-color: $white;
  position: absolute;
  height: 2px;
  width: 22.5px;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
}

.hamburger__menu-button::before {
  content: "";
  margin-top: -8px;
}

.hamburger__menu-button::after {
  content: "";
  margin-top: 8px;
}

#hamburger__menu-toggle:checked
  + .hamburger__menu-button-container
  .hamburger__menu-button::before {
  margin-top: 0px;
  transform: rotate(45deg);
}

#hamburger__menu-toggle:checked
  + .hamburger__menu-button-container
  .hamburger__menu-button {
  background: rgba(255, 255, 255, 0);
}

#hamburger__menu-toggle:checked
  + .hamburger__menu-button-container
  .hamburger__menu-button::after {
  margin-top: 0px;
  transform: rotate(-45deg);
}

.info-block {
  padding: 62px 80px 60px;

  @media (max-width: $screen-xl-min) {
    padding: 55px 30px 53px;
  }

  @media (max-width: $screen-md-min) {
    padding: 50px 20px 50px 22px;
  }
}

.info__title {
  font-size: 32px;
  font-weight: 800;
  line-height: 38px;
  text-align: left;
  color: $white;

  @media (max-width: $screen-xl-min) {
    font-size: 24px;
    line-height: 29px;
  }

  @media (max-width: $screen-md-min) {
    font-size: 18px;
    line-height: 22px;
  }
}

.info__description {
  margin-top: 17px;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  color: $white;
  opacity: 0.6;

  @media (max-width: $screen-xl-min) {
    font-size: 16px;
    line-height: 19px;
    margin-top: 13px;
  }

  @media (max-width: $screen-md-min) {
    font-size: 14px;
    line-height: 17px;
    margin-top: 20px;
  }
}

@media (max-width: $screen-md-min) {
  #hamburger__menu-toggle ~ .hamburger__menu li {
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  #hamburger__menu-toggle:checked ~ .hamburger__menu li {
    &:last-child {
      border-bottom: 1px solid $white;
    }
    border-top: 1px solid $white;
    height: 2.5em;
    padding: 0.5em;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  .hamburger__menu > li:not(:last-child) {
    border-bottom: 1px solid $white;
  }
}

.info-block_hidden {
  display: none;
}

.read-more:not(:checked) ~ label,
.read-more:checked ~ .info-block_hidden {
  display: block;
}

.read-more,
.read-more:checked ~ label {
  display: none;
}

.box {
  display: inline-block;
  height: 50px;
  width: 50px;
  margin: 5px 0 0 5px;
  background-color: red;
}

.read-more-label {
  cursor: pointer;
  margin: 17px 0 0;
  font-size: 20px;
  font-weight: 800;
  line-height: 24px;
  text-align: left;
  color: $yellow;
  &:hover {
    color: $transparentYellow;
  }

  @media (max-width: $screen-xl-min) {
    font-size: 16px;
    line-height: 19px;
    margin: 20px 0 0;
  }

  @media (max-width: $screen-md-min) {
    font-size: 14px;
    line-height: 17px;
  }
}
